<template lang="pug">
  .sign-up
    .column.is-12
      // <h3 class="title has-text-grey">Sign up</h3>
      transition(
        name="slide-fade"
        mode="out-in")
        .card.shadow.px-4.py-4.gdpr-consent(v-if="!gdprConsented" key="consent")
          gdpr-consent(v-if="!gdprConsented" v-model="gdprConsented")
        .card.shadow.px-4.py-4.sign-up-form(v-else key="sign-up")
          b-form(@submit="onSubmit" :class="{ isLoading }")
            .btn.btn-outline-primary.w-100(v-google-signin-button="googleSignInParams.client_id" @click.native="googleIsLoading = true")
              | Sign up with Google
            p.text-center.my-2
              | or

            .message.is-danger(v-if="error")
              .message-body
                | {{ error }}
            b-form-group(label="First name:" label-for="first_name")
              b-form-input#first_name(
                @keyup.native.enter="onSubmit"
                :state="$v.form.first_name.$dirty ? !$v.form.first_name.$error : null"
                v-model="form.first_name" required="" autofocus="" size="is-medium" placeholder="First name")
              b-form-invalid-feedback
                | Please fill this field

            b-form-group(label="Email:" label-for="email")
              b-form-input#email(
                @keyup.native.enter="onSubmit"
                :state="$v.form.email.$dirty ? !$v.form.email.$error : null"
                v-model="form.email" type="email" required="" autofocus="" size="is-medium" placeholder="Email")
              b-form-invalid-feedback
                | Please fill this field with a valid email address
            b-form-group(label="Password:" label-for="password")
              b-form-input#password(
                @keyup.native.enter="onSubmit"
                :state="$v.form.password.$dirty ? !$v.form.password.$error : null"
                v-model="form.password" type="password" required="" password-reveal="" size="is-medium" placeholder="Password")
              b-form-invalid-feedback
                | Please use at least 6 characters
            b-form-group
              b-form-input#confirm_password(
                @keyup.native.enter="onSubmit"
                :state="$v.form.confirm_password.$dirty ? !$v.form.confirm_password.$error : null"
                v-model="form.confirm_password" type="password" password-reveal="" required="" size="is-medium" placeholder="Confirm password")
              b-form-invalid-feedback
                | Please type your password a second time
            b-form-group.has-text-left.mt-3
              b-form-checkbox(v-model="form.checked")
                | Remember me
            .text-right.mt-4
              b-button#btn_sign_up(
                variant="primary"
                :class="{'is-loading': isLoading, 'is-disabled': isLoading}"
                @click="onSubmit($event)")
                | Sign up
</template>


<script>
  import gdprConsent from "@/components/GdprConsent";
  import GoogleSignInButton from 'vue-google-signin-button-directive'

  import { validationMixin } from 'vuelidate'
  import { required, minLength, email, sameAs } from 'vuelidate/lib/validators'

  import { mapActions } from "vuex";

  export default {
    name: 'SignUp',
    components: {
      gdprConsent
    },
    directives: {
      GoogleSignInButton
    },
    mixins: [validationMixin],
    data() {
      return {
        gdprConsented: this.$cookie.get('gdprConsented'),
        error: null,
        form: {
          first_name: '',
          email: '',
          username: '',
          password: '',
          confirm_password: ''
        },
        redirect: null,
        isLoading: false,
        googleIsLoading: false,
        googleSignInParams: {
          client_id: '476667578043-0tm7eoufgmi6m961qb2m77hhelj5ksl5.apps.googleusercontent.com',
          response_type: 'permission'
        }
      }
    },
    validations: {
      form: {
        first_name: {
          required
        },
        email: {
          required,
          email
        },
        password: {
          required,
          minLength: minLength(6)
        },
        confirm_password: {
          required,
          minLength: minLength(6),
          sameAsPassword: sameAs('password')
        }
      }
    },
    created() {
    },

    methods: {
      ...mapActions(['setLayout']),
      onSubmit(evt) {
        this.$v.form.$touch()
        if (this.$v.form.$anyError) {
          return
        }

        this.isLoading = true
        if (evt) {
          evt.preventDefault()
        }
        this.$auth.register({
          body: {user: this.form},
          autoLogin: true,
          redirect: this.redirect || {name: 'NewService'},
          rememberMe: this.form.checked
        }).then((res) => {
          this.$mixpanel.alias(res.body.data.id);
          document.querySelector('button.modal-close').click();
          console.log({res})
          this.$store.dispatch("setUser", res.body.data);
        }, (res) => {
          console.log('error ' + this.context);
          this.error = res.data.message;
          this.isLoading = false
        });
      },
      OnGoogleAuthSuccess(access_token) {
        this.$store.dispatch("setUserFromGoogle", {
          access_token,
          pid: this.form.pid,
        }).then((res) => {
          if (!res.userExisted) {
            this.$mixpanel.alias(res.user.id + '');
          }
          this.$store.dispatch("setUser", res.user);
          this.$auth.user(res.user)
          this.$auth.token(null, res.token)
          this.$auth.watch.authenticated = true
          this.$auth.watch.loaded = true
          this.googleIsLoading = false;
          this.$cookie.set('rememberMe', true, 365)
          // this.$router.push({name: 'Home'})
          this.$router.push({name: 'FrontPage'});
          document.querySelector('button.modal-close').click()
        });
      },
      OnGoogleAuthFail(error) {
        console.log(error)
        this.googleIsLoading = false;
      }
    }
    /*created(evt) {
    console.log(evt)
      evt.preventDefault()
      axios.post(`http://localhost:3003/api/register`, this.form)
      .then(response => {
        // JSON responses are automatically parsed.
        this.form = response.data
      })
      .catch(e => {
        console.log(e)
      })


    },*/
  }
</script>


<style lang="scss">

  .sign-up {

    input {
      font-weight: 300;
    }

    label:not(.custom-control-label) {
      text-transform: uppercase;
      font-weight:    700;
      font-size:      0.9rem;
      color:          #636573
    }

    .isLoading {
      .was-validated .form-control:valid, .form-control.is-valid {
        border: 0 none;
        background-image: none;
      }
    }

    p.subtitle {
      padding-top: 1rem;
    }

    a {
      text-decoration: none;
    }

    .sign-up-form {
      width:  300px;
      margin: 100px auto;
    }

  }

</style>
